<template>
    <div class="backcolorlogin">
        <div class="login">



            <el-row :gutter="50" style="max-width: 768px;min-width: 730px;margin: 0 auto">
                <el-col :xs="24" :sm="12" :md="12" :lg="12" :xl="12">
                    <div class="grid-content bg-purple purple-width-one" style="min-width: 340px">

                        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" >
                            <el-form-item prop="adminName" label="账号">
                                <el-input placeholder="请输入您尊贵的账号^_^" type="text" v-model="ruleForm.adminName" style="width: 340px">
                                </el-input>
                            </el-form-item>

                            <el-form-item prop="passWord" label="密码">
                                <el-input placeholder="请输入您珍藏的密码^_^" type="password" v-model="ruleForm.passWord" @keyup.enter.native="submitForm('ruleForm')" style="width: 340px">
                                </el-input>
                            </el-form-item>

                            <el-form-item>
                                <el-button type="primary" round @click="submitForm('ruleForm')" :loading="loading">点击登录</el-button>

                            </el-form-item>
                            <el-form-item>
                                <el-button type="primary" round @click="resetForm('ruleForm')">重置</el-button>
                            </el-form-item>

                        </el-form>
                    </div>
                </el-col>
                <el-col :xs="24" :sm="12" :md="12" :lg="12" :xl="12">
                    <div class="grid-content bg-purple purple-width" style="min-width: 340px">

                        <h3>没有账号怎么办?</h3>
                        <p>拥有账号后您将拥有以下权利</p>
                        <ul>
                            <li>新用户优惠券</li>
                            <li>个性化推荐内容</li>
                            <li>订单跟踪</li>
                        </ul>
                        <el-button type="primary" round @click="">创建</el-button>



                    </div></el-col>

            </el-row>

        </div>



    </div>

</template>

<script>
    import 'element-ui/lib/theme-chalk/index.css';
    export default {
        name: "login-content",
        data() {
            return {
                loading:false,
                ruleForm: {
                    adminName: '',
                    passWord: ''
                },
                rules: {
                    adminName: [
                        {required: true, message: '请您输入账号', trigger: 'blur'},
                        {min: 2, max: 8, message: '长度在 2 到 8 个字符', trigger: 'blur'}
                    ],
                    passWord: [
                        {required: true, message: '请您输入密码', trigger: 'blur'},
                        {min: 3, max: 8, message: '长度在 3 到 8 个字符', trigger: 'blur'}
                    ]
                }
            }
        },
        methods: {
            submitForm(formName) {

                //对表单进行验证，并放到回调函数中
                this.$refs[formName].validate((valid) => {

                    console.log(valid);

                    if (valid) {//输入的内容符合要求

                        this.loading=true;

                        this.$store.dispatch("adminLogin",{
                            data:{
                                adminName:this.ruleForm.adminName,
                                passWord:this.ruleForm.passWord
                            },
                            success:()=>{
                                console.log(this)
                                this.loading = false;
                            }

                        })
                        // alert('submit!');
                    } else {//输入的内容不符合要求
                        // console.log('error submit!!');
                        // return false;
                        this.$message({
                            showClose: true,
                            message: '请您输入正确的验证信息',
                            type: 'error'
                        });
                    }
                });
            },

            //重置按钮
            resetForm(formName) {
                this.loading=false;
                this.$refs[formName].resetFields();
            }
        }
    }
</script>

<style lang="less" scoped>
    .backcolorlogin{
        background: #E7E7E7
    }
    .login{
        background: #E7E7E7;
        overflow: hidden;
        padding: 60px 70px 100px 0;
        max-width: 1200px;
        margin: 0 auto;




        .el-row {
            margin-bottom: 20px;
            &:last-child {
                margin-bottom: 0;
            }
        }
        .el-col {
            border-radius: 4px;
        }
        .bg-purple-dark {
            background: #99a9bf;
        }
        .bg-purple {
            /*background: #d3dce6;*/
        }
        .purple-width-one{
            /*width: 75%;*/
            /*margin: 0 auto;*/
            /*padding-left: 100px;*/
        }
        .bg-purple-light {
            background: #e5e9f2;
        }
        .grid-content {
            border-radius: 4px;
            min-height: 36px;
        }
        .row-bg {
            padding: 10px 0;
            background-color: #f9fafc;
        }

    }
    .el-button{
        width: 100%;
        background: #1B1B1B;
        border: none;
        margin: 10px auto;

    }


</style>